.datasource-popup-model{background:#fff;border-radius:3px}.datasource-popup-model .top-p{height:70px;line-height:70px;border-radius:3px 3px 0 0;padding:0 20px}.datasource-popup-model .top-p>span{font-size:20px}.datasource-popup-model .bottom-p{text-align:right;height:72px;line-height:72px;border-radius:0 0 3px 3px;padding:0 20px}.datasource-popup-model .content-p{min-width:850px;min-height:100px}.datasource-popup-model .content-p .list-box-f .text{width:166px}.datasource-popup-model .content-p .list-box-f .cont{width:calc(100% - 186px)}.datasource-popup-model .radio-label-last{margin-left:0!important}